import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Table, Alert, Divider, Modal, Button } from 'antd';
import { ColumnProps, TableProps } from 'antd/lib/table';
import ReasonForm from './ReasonForm';
import styles from './SearchResult.module.scss';

const { confirm } = Modal;

interface Order {
  ordercode: string;
  name: string;
  fj: boolean;
  mobile: string;
  kq: string;
  amount: number;
  term: number;
  zjtd: string;
  created_time: string;
  fk_time: string;
  status: string;
}

interface Props {
  dataSource?: Order[];
}

export default class SearchResult extends Component<Props, any> {
  rowKey: TableProps<Order>['rowKey'] = record => record.ordercode;

  render() {
    return (
      <>
        <Alert message="共计5笔，总计4324235元" type="info" showIcon className={styles.alert} />
        <Table
          columns={columns}
          dataSource={data}
          rowKey={this.rowKey}
          scroll={{ x: 'max-content' }}
          pagination={{ showTotal: total => `共${total}条记录`, showQuickJumper: true }}
        />
      </>
    );
  }
}

const columns: Array<ColumnProps<Order>> = [
  {
    title: '序号',
    key: 'index',
    fixed: 'left',
    width: 60,
    render: (text, record, index) => index + 1
  },
  {
    title: '订单编号/申请单号',
    dataIndex: 'ordercode',
    key: 'age',
    render: text => <Link to={`/order/${text}`}>{text}</Link>
  },
  {
    title: '借款人',
    dataIndex: 'name',
    key: 'name'
  },
  {
    title: '复借用户',
    key: 'fj',
    dataIndex: 'fj',
    render: (text, record) => (record.fj ? '是' : '否')
  },
  {
    title: '手机号',
    dataIndex: 'mobile',
    key: 'mobile'
  },
  {
    title: '客群',
    key: 'kq',
    dataIndex: 'kq'
  },
  {
    title: '借款金额',
    key: 'amount',
    dataIndex: 'amount'
  },
  {
    title: '借款期限',
    key: 'term',
    dataIndex: 'term'
  },
  {
    title: '资金通道',
    key: 'zjtd',
    dataIndex: 'zjtd'
  },
  {
    title: '订单创建时间',
    key: 'created_time',
    dataIndex: 'created_time'
  },
  {
    title: '订单放款时间',
    key: 'fk_time',
    dataIndex: 'fk_time'
  },
  {
    title: '订单状态',
    key: 'status',
    dataIndex: 'status'
  },
  {
    title: '操作',
    key: 'operation',
    fixed: 'right',
    render: (text, record) => (
      <span>
        <Button
          type="link"
          // tslint:disable-next-line: jsx-no-lambda
          onClick={() => {
            confirm({
              iconType: 'exclamation-circle',
              title: '您将发起重新放款',
              content: <ReasonForm dataSource={record} />
            });
          }}
        >
          重新放款
        </Button>
        <Divider type="vertical" />
        <Button
          type="link"
          // tslint:disable-next-line: jsx-no-lambda
          onClick={() => {
            confirm({
              iconType: 'exclamation-circle',
              title: '您将发起重新放款',
              content: <ReasonForm dataSource={record} hasReason />
            });
          }}
        >
          终止订单
        </Button>
      </span>
    )
  }
];

