import React, { Component } from 'react';
import { Form, Row, Col, Input, Button, Select, DatePicker, Checkbox } from 'antd';
import { WrappedFormInternalProps, FormProps } from 'antd/lib/form/Form';
import styles from './SearchForm.module.scss';

const { Option } = Select;
const { RangePicker } = DatePicker;

interface Props extends WrappedFormInternalProps {
  onSubmit?: FormProps['onSubmit'];
}

class SearchForm extends Component<Props, any> {
  handleSearch: FormProps['onSubmit'] = e => {
    const { form, onSubmit } = this.props;
    e.preventDefault();
    form.validateFields((err, values) => {
      console.log('Received values of form: ', values, err);
      if (typeof onSubmit === 'function') {
        onSubmit(e);
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const {
      form: { getFieldDecorator }
    } = this.props;

    return (
      <Form className="ant-advanced-search-form" onSubmit={this.handleSearch} layout="vertical" colon>
        <Row gutter={24}>
          <Col span={8} key="ordercode">
            <Form.Item label="订单编号" className={styles.item}>
              {getFieldDecorator('ordercode')(<Input placeholder="请输入订单编号/申请单号" allowClear />)}
            </Form.Item>
          </Col>
          <Col span={8} key="name">
            <Form.Item label="姓名" className={styles.item}>
              {getFieldDecorator('name')(<Input placeholder="请输入姓名" allowClear />)}
            </Form.Item>
          </Col>
          <Col span={8} key="mobile">
            <Form.Item label="手机号" className={styles.item}>
              {getFieldDecorator('mobile')(<Input placeholder="请输入手机号" allowClear type="phone" maxLength={11} />)}
            </Form.Item>
          </Col>
          <Col span={8} key="zjtd">
            <Form.Item label="资金通道" className={styles.item}>
              {getFieldDecorator('zjtd', { initialValue: '0' })(
                <Select>
                  <Option value="0">全部</Option>
                  <Option value="1">贝壳小贷</Option>
                  <Option value="2">博能小贷</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="kq">
            <Form.Item label="客群" className={styles.item}>
              {getFieldDecorator('kq', { initialValue: '0' })(
                <Select>
                  <Option value="0">全部</Option>
                  <Option value="1">贝壳金服</Option>
                  <Option value="2">链家经纪人</Option>
                  <Option value="3">贝壳经纪人</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="fjyh">
            <Form.Item label="复借用户" className={styles.item}>
              {getFieldDecorator('fjyh', { initialValue: '0' })(
                <Select>
                  <Option value="0">全部</Option>
                  <Option value="1">是</Option>
                  <Option value="2">否</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="ddzt">
            <Form.Item label="订单状态" className={styles.item}>
              {getFieldDecorator('ddzt', { initialValue: '0' })(
                <Select>
                  <Option value="0">全部</Option>
                  <Option value="1">风控审核中</Option>
                  <Option value="2">风控审核成功</Option>
                  <Option value="3">放款审核中</Option>
                  <Option value="4">放款审核失败</Option>
                  <Option value="5">放款中</Option>
                  <Option value="6">放款成功</Option>
                  <Option value="7">放款失败</Option>
                  <Option value="8">已还清</Option>
                  <Option value="9">订单终止</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8} key="ddcjsj">
            <Form.Item label="订单创建时间" className={styles.item}>
              {getFieldDecorator('ddcjsj')(<RangePicker style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={8} key="ddfksj">
            <Form.Item label="订单放款时间" className={styles.item}>
              {getFieldDecorator('ddfksj')(<RangePicker style={{ width: '100%' }} />)}
            </Form.Item>
          </Col>
          <Col span={24} key="kssx">
            <Form.Item label="快速筛选" className={styles.item}>
              {getFieldDecorator('kssx')(
                <Checkbox.Group style={{ width: '100%' }}>
                  <Checkbox value="A">逾期订单</Checkbox>
                  <Checkbox value="B">今日到期尚未还款</Checkbox>
                </Checkbox.Group>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">
              查询
            </Button>
            <Button style={{ marginLeft: 8 }} onClick={this.handleReset}>
              重置
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default Form.create<Props>({ name: 'order-search-form' })(SearchForm);
