import React from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { LocaleProvider } from 'antd';
import moment from 'moment';
import 'moment/locale/zh-cn';
import zhCN from 'antd/es/locale-provider/zh_CN';
// import CmsLayout1 from '../layout/CmsLayout1';
// pages
import Search from '../pages/order/Search';
import Detail from '../pages/order/Detail';

moment.locale('zh-cn');

const Pages = (
  <Switch>
    <Redirect from="/" to="/byjorder" exact />
    <Route path="/byjorder" component={Search} exact />
    <Route path="/byjorder/:code" component={Detail} />
  </Switch>
);

export default function App() {
  return (
    <LocaleProvider locale={zhCN}>
      <Router>
        {/* <CmsLayout1 routes={[{ name: '订单管理', icon: 'user', key: '/byjorder', path: '/byjorder' }]}>{Pages}</CmsLayout1> */}
        {Pages}
      </Router>
    </LocaleProvider>
  );
}
