import React from 'react';
import { Form, Input } from 'antd';
import { FormComponentProps } from 'antd/lib/form';

const { TextArea } = Input;

interface Props<T = any> extends FormComponentProps {
  hasReason?: boolean;
  dataSource: T;
}

function ConfirmContent(props: Props) {
  const {
    hasReason,
    dataSource,
    form: { getFieldDecorator }
  } = props;

  return (
    <>
      <p>订单编号：{dataSource.ordercode}</p>
      <p>金额：{dataSource.amount}元</p>
      {hasReason ? (
        <Form layout="inline" labelCol={{ span: 4 }} wrapperCol={{ span: 20 }}>
          <Form.Item label="原因" style={{ width: '100%' }}>
            {getFieldDecorator('reason', { rules: [{ required: true, message: '请填写终止订单原因！' }] })(
              <TextArea placeholder="请填写终止订单原因" autosize={{ minRows: 4, maxRows: 8 }} />
            )}
          </Form.Item>
        </Form>
      ) : null}
    </>
  );
}

export default Form.create<Props>({ name: 'reason' })(ConfirmContent);
